.content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -17px;
  margin-right: -17px; }
  @media screen and (max-width: 767px) {
    .content {
      margin-left: 0px;
      margin-right: 0px; } }
  .content .title__block {
    width: 100%;
    padding-left: 17px;
    padding-right: 17px; }
    @media screen and (max-width: 767px) {
      .content .title__block {
        padding-left: 0px;
        padding-right: 0px; } }
  .content .content__left {
    width: calc(8.33333% * 8);
    padding-left: 17px;
    padding-right: 17px; }
    @media screen and (max-width: 1200px) {
      .content .content__left {
        width: calc(8.33333% * 7); } }
    @media screen and (max-width: 767px) {
      .content .content__left {
        width: 100%;
        padding-left: 0px;
        padding-right: 0px; } }
  .content .content__right {
    width: calc(8.33333% * 4);
    padding-left: 17px;
    padding-right: 17px; }
    @media screen and (max-width: 1200px) {
      .content .content__right {
        width: calc(8.33333% * 5); } }
    @media screen and (max-width: 767px) {
      .content .content__right {
        width: 100%;
